body {
  margin: 0;
  padding: 0;
}
img {
	max-width: 100%;
}
#ProfileImageWatermarker {
  text-align: center;
  background-color: red;
  width: 750px;
  margin: auto;
}
#watermarkOptions{
  background-color: green;
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.watermarkOption{
  background-color: blue;
  height: 100%;
}
#imageContainer{
  background-color: green;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.cropBox{
  background-color: orange;
  width: 59%;
  margin: auto;
}
.previewBox{
  background-color: orange;
  width: 39%;
  margin: auto;
  position: relative;
}
#cropWatermark {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
#cropPreview{
  width: 100%;
}

#transparencySlider {
  width: 80%;
  margin: 10px auto;
}

.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 10px;
  border-radius: 5px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border: 1px solid black;
  border-radius: 50%;
  background-color: yellow;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border: 1px solid black;
  border-radius: 50%;
  background-color: yellow;
  cursor: pointer;
}

@media only screen and (max-width: 750px) {
  #ProfileImageWatermarker {
    width: 100vw;
  }
}
